
import { defineComponent, onMounted, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { GlobalDataProps } from '../store'
import PostList from '../components/PostList.vue'
import useLoadMore from '@/hook/useLoadMore'

export default defineComponent({
  components: {
    PostList
  },
  setup () {
    const route = useRoute()
    const store = useStore<GlobalDataProps>()
    const currentId = route.params.id
    const currentPage = computed(() => store.state.posts.currentPage)
    const total = computed(() => store.state.posts.total)

    onMounted(() => {
      store.dispatch('fetchColumn', currentId)
      store.dispatch('fetchPosts', { pageSize: 2, cid: currentId })
    })

    const column = computed(() => store.getters.getColumnById(currentId))
    const list = computed(() => store.getters.getPostsByCid(currentId))

    const { isLastPage, loadMorePage } = useLoadMore('fetchPosts',
      total, { cid: currentId, currentPage: currentPage.value ? currentPage.value + 1 : 2, pageSize: 2 })

    return {
      column,
      list,
      isLastPage,
      loadMorePage
    }
  }
})
