import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/views/Home.vue'
import Login from '@/views/Login.vue'
import ColumnDetail from '@/views/ColumnDetail.vue'
import CreatePost from './views/CreatePost.vue'
import Signup from './views/Signup.vue'
import store from './store'
import axios from 'axios'
import PostDetail from '@/views/PostDetail.vue'
import Edit from '@/views/Edit.vue'

const routerHistory = createWebHistory()
const router = createRouter({
  history: routerHistory,
  routes: [
    {
      path: '/zheye-zhuanlan-build',
      name: 'home',
      component: Home
    },
    {
      path: '/zheye-zhuanlan-build/login',
      name: 'login',
      component: Login,
      meta: {
        redirectAlreadyLogin: true
      }
    },
    {
      path: '/zheye-zhuanlan-build/signup',
      name: 'signup',
      component: Signup,
      meta: {
        redirectAlreadyLogin: true
      }
    },
    {
      path: '/zheye-zhuanlan-build/column/:id',
      name: 'column',
      component: ColumnDetail
    },
    {
      path: '/zheye-zhuanlan-build/create',
      name: 'create',
      component: CreatePost,
      meta: {
        requiredLogin: true
      }
    },
    {
      path: '/zheye-zhuanlan-build/posts/:id',
      name: 'post',
      component: PostDetail,
      meta: {
        requiredLogin: true
      }
    },
    {
      path: '/zheye-zhuanlan-build/edit',
      name: 'edit',
      component: Edit,
      meta: {
        requiredLogin: true
      }
    }
  ]
})
router.beforeEach((to, from, next) => {
  const { user, token } = store.state
  const { redirectAlreadyLogin, requiredLogin } = to.meta

  if (!user.isLogin) {
    if (token) {
      axios.defaults.headers.common.Authorization = `Bearer ${token}`
      store.dispatch('fetchCurrentUser').then(() => {
        if (redirectAlreadyLogin) {
          next('/zheye-zhuanlan-build')
        } else {
          next()
        }
      }).catch(e => {
        console.error(e)
        localStorage.removeItem('token')
        next('/zheye-zhuanlan-build/login')
      })
    } else {
      if (requiredLogin) {
        next('/zheye-zhuanlan-build/login')
      } else {
        next()
      }
    }
  } else {
    if (redirectAlreadyLogin) {
      next('/zheye-zhuanlan-build')
    } else {
      next()
    }
  }
})

export default router
