<template>
  <nav class="navbar navbar-dark bg-primary justify-content-between mb-4 px-4">
    <router-link class="navbar-brand" to="/"></router-link>
    <ul v-if="!user.isLogin" class="list-inline mb-0">
      <li class="list-inline-item"><router-link to="/login" class="btn btn-outline-light my-2"></router-link></li>
      <li class="list-inline-item"><router-link to="/signup" class="btn btn-outline-light my-2"></router-link></li>
    </ul>
    <ul v-else class="list-inline-item mb-0">
      <li class="list-inline-item">
        <dropdown :title="` ${user.nickName}`">
          <dropdown-item>
            <router-link to="/create" class="dropdown-item"></router-link>
          </dropdown-item>
          <dropdown-item v-if="user.column">
            <router-link :to="`/column/${user.column}`" class="dropdown-item"></router-link>
          </dropdown-item>
          <dropdown-item>
            <router-link to="/edit" class="dropdown-item"></router-link>
          </dropdown-item>
          <dropdown-item @click="signOut">
            <a href="#" class="dropdown-item"></a>
          </dropdown-item>
        </dropdown>
      </li>
    </ul>
  </nav>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue'
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'
import Dropdown from './Dropdown.vue'
import DropdownItem from './DropdownItem.vue'
import { UserProps } from '../store'

export default defineComponent({
  name: 'GlobalHeader',
  components: {
    Dropdown,
    DropdownItem
  },
  props: {
    user: {
      type: Object as PropType<UserProps>,
      required: true
    }
  },
  setup () {
    const router = useRouter()
    const store = useStore()
    const signOut = () => {
      store.commit('logOut')
      router.push('/login')
    }

    return {
      signOut
    }
  }
})
</script>

