<template>
  <div>
    <modal :is-visable="isModalVisiable"
      title=""
      @modal-on-close="isModalVisiable = false"
      @modal-on-confirm="deletePost">
      <p></p>
    </modal>
    <article>
      <img
        v-if="currentImageUrl"
        class="rounded-lg img-fluid my-4"
        :src="currentImageUrl">
      <h2 class="mb-3">{{ currentPost.title }}</h2>
      <div class="post-info-row border-top border-bottom mb-5">
        <user-profile :user="currentPost.author" v-if="typeof currentPost.author === 'object'"></user-profile>
        <span class="text-muted">{{`${currentPost ? currentPost.createdAt :''}`}}</span>
      </div>
      <div v-html="contentHtml"></div>
      <div v-if="showEditArea">
        <router-link
          type="button"
          class="btn btn-success"
          :to="{ name: 'create', query: { id: currentPost._id }}">
        </router-link>
        <button class="btn btn-danger" @click="isModalVisiable = true"></button>
      </div>
    </article>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType, onMounted, ref } from 'vue'
import UserProfile from '@/components/UserProfile.vue'
import Modal from '@/components/Modal.vue'
import { PostProps, ImageProps, UserProps } from '@/store'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from 'vuex'
import MarkdownIt from 'markdown-it'
import createMessage from '@/components/createMessage'

export default defineComponent({
  name: 'post-detail',
  props: {
    post: {
      type: Object as PropType<PostProps>,
      required: false
    }
  },
  components: { UserProfile, Modal },
  setup () {
    const route = useRoute()
    const store = useStore()
    const router = useRouter()

    const isModalVisiable = ref(false)
    const { column } = store.state.user

    const postId = route.params.id
    onMounted(() => {
      store.dispatch('fetchPost', postId)
    })

    const currentPost = computed<PostProps>(() => store.getters.getCurrentPost(postId))
    const currentImageUrl = computed(() => {
      if (currentPost.value && currentPost.value.image) {
        const { image } = currentPost.value
        return (image as ImageProps).url + '?x-oss-process=image/resize,w_850'
      } else {
        return null
      }
    })
    const contentHtml = computed(() => {
      if (currentPost.value && currentPost.value.content) {
        if (currentPost.value.content.includes('<p>')) {
          return currentPost.value.content
        }
        return new MarkdownIt().render(currentPost.value.content)
      } else {
        return ''
      }
    })

    const showEditArea = computed(() => {
      const { _id, isLogin } = store.state.user
      if (currentPost.value && currentPost.value.author && isLogin) {
        const currentPostAuthor = currentPost.value.author as UserProps
        return currentPostAuthor._id === _id
      }
      return false
    })

    const deletePost = () => {
      isModalVisiable.value = false
      store.dispatch('deletePost', postId).then(() => {
        debugger
        createMessage(' 2 ', 'success', 2000)
        setTimeout(() => {
          router.push({ name: 'column', params: { id: column } })
        }, 2000)
      })
    }

    return {
      currentPost,
      currentImageUrl,
      contentHtml,
      showEditArea,
      isModalVisiable,
      deletePost
    }
  }
})
</script>

<style scoped>
.post-info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
}

article {
  margin-bottom: 48px;
  padding-bottom: 16px;
}
</style>
