<template>
  <div class="column-detail-page w-75 mx-auto">
    <div class="column-info row mb-4 border-bottom pb-4 align-items-center" v-if="column">
      <div class="col-3 text-center">
        <img :src="column.avatar && column.avatar.url"
          :alt="column.title"
          class="rounded-circle border w-100">
      </div>
      <div class="col-9">
        <h4>{{column.title}}</h4>
        <p class="text-muted">{{column.description}}</p>
      </div>
    </div>
    <post-list :list="list"></post-list>
    <div class="d-grid gap-1">
      <button
        class="btn btn-outline-primary mt-2 mb-5 mx-auto btn-block w-25"
        @click="loadMorePage" v-if="!isLastPage">
        
      </button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { GlobalDataProps } from '../store'
import PostList from '../components/PostList.vue'
import useLoadMore from '@/hook/useLoadMore'

export default defineComponent({
  components: {
    PostList
  },
  setup () {
    const route = useRoute()
    const store = useStore<GlobalDataProps>()
    const currentId = route.params.id
    const currentPage = computed(() => store.state.posts.currentPage)
    const total = computed(() => store.state.posts.total)

    onMounted(() => {
      store.dispatch('fetchColumn', currentId)
      store.dispatch('fetchPosts', { pageSize: 2, cid: currentId })
    })

    const column = computed(() => store.getters.getColumnById(currentId))
    const list = computed(() => store.getters.getPostsByCid(currentId))

    const { isLastPage, loadMorePage } = useLoadMore('fetchPosts',
      total, { cid: currentId, currentPage: currentPage.value ? currentPage.value + 1 : 2, pageSize: 2 })

    return {
      column,
      list,
      isLastPage,
      loadMorePage
    }
  }
})
</script>
