<template>
  <div class="home-page">
    <section class="py-5 text-center container">
      <div class="row py-lg-5">
        <div class="col-lg-6 col-md-8 mx-auto">
          <img src="../assets/callout.svg" alt="callout" class="w-50"/>
          <h2 class="font-weight-light"></h2>
          <p>
            <a href="#" class="btn btn-primary my-2"></a>
          </p>
        </div>
      </div>
    </section>
    <h4 class="font-weight-bold text-center"></h4>
    <column-list :list="list"></column-list>
    <div class="d-grid gap-1">
      <button
        class="btn btn-outline-primary mt-2 mb-5 mx-auto btn-block w-25"
        @click="loadMorePage" v-if="!isLastPage">
        
      </button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { GlobalDataProps } from '../store'
import ColumnList from '@/components/ColumnList.vue'
import useLoadMore from '@/hook/useLoadMore'

export default defineComponent({
  name: 'Home',
  components: {
    ColumnList
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    const total = computed(() => store.state.columns.total)
    const currentPage = computed(() => store.state.columns.currentPage)
    onMounted(() => {
      store.dispatch('fetchColumns', { pageSize: 3 })
    })
    const list = computed(() => store.getters.getColumns)

    const { isLastPage, loadMorePage } = useLoadMore('fetchColumns',
      total, { currentPage: currentPage.value ? currentPage.value + 1 : 2, pageSize: 3 })

    return {
      list,
      isLastPage,
      loadMorePage
    }
  }
})
</script>
