<template>
  <ul class="nav">
    <li :class="editMode === 'user' ? 'nav-active' : ''"
        @click="editMode = 'user'">
        
    </li>
    <li :class="editMode === 'column' ? 'nav-active' : ''"
        @click="editMode = 'column'">
      
    </li>
  </ul>
  <div>
    <h4>{{ editMode === 'user' ? '' : '' }}</h4>
    <uploader
      :before-upload="beforeUpload"
      @file-uploaded="handleFileUploaded"
      :uploaded="uploadedData"
      action="/upload"
      class="d-flex align-items-center justify-content-center bg-light text-secondary circle mx-auto my-3">
      <h2></h2>
      <template #loading>
        <div class="d-flex">
          <div class="spinner-border text-secondary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
          <h2></h2>
        </div>
      </template>
      <template #uploaded="dataProps">
        <img :src="dataProps.uploadedData.data.url"/>
      </template>
    </uploader>
    <validate-form @form-submit="onFormSubmit">
      <div class="mb-3">
        <validate-input
          placeholder=""
          type="text"
          v-model="nameVal"
          :rules="titleRules"
        />
      </div>
      <div class="mb-3">
        <validate-input
          rows="10"
          placeholder=""
          type="text"
          tag="textarea"
          v-model="descriptionVal"
          :rules="descriptionRules"
        />
      </div>
      <template #submit>
        <button class="btn btn-primary btn-large"></button>
      </template>
    </validate-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, computed, ref, WritableComputedRef } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import ValidateInput, { RulesProp } from '@/components/ValidateInput.vue'
import ValidateForm from '@/components/ValidateForm.vue'
import Uploader from '@/components/Uploader.vue'
import { GlobalDataProps, ResponseType, ImageProps } from '@/store'
import createMessage from '@/components/createMessage'
import { beforeUploadCheck } from '@/utils/helper'

export default defineComponent({
  name: 'Edit',
  components: {
    ValidateForm,
    ValidateInput,
    Uploader
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    const user = computed(() => store.state.user)
    const columnId = computed(() => user.value.column)
    const titleVal = ref('')
    const columnDescriptionVal = ref('')
    const column = computed(() => store.getters.getColumnById(columnId.value))
    const uploadedData = ref({ data: {} })
    const editMode = ref('user')
    const nickNameVal = ref('')
    const userDescriptionVal = ref('')
    const router = useRouter()

    const nameVal: WritableComputedRef<string> = computed({
      get (): string {
        return editMode.value === 'user' ? nickNameVal.value : titleVal.value
      },
      set (newValue): void {
        if (editMode.value === 'user') {
          nickNameVal.value = newValue
        } else {
          titleVal.value = newValue
        }
      }
    })

    const descriptionVal: WritableComputedRef<string> = computed({
      get (): string {
        return editMode.value === 'user' ? userDescriptionVal.value : columnDescriptionVal.value
      },
      set (newValue): void {
        if (editMode.value === 'user') {
          userDescriptionVal.value = newValue
        } else {
          columnDescriptionVal.value = newValue
        }
      }
    })

    onMounted(() => {
      nickNameVal.value = user.value.nickName || ''
      userDescriptionVal.value = user.value.description || ''

      store.dispatch('fetchColumn', columnId.value).then(() => {
        titleVal.value = column.value.title
        columnDescriptionVal.value = column.value.description
        uploadedData.value = {
          data: column.value.avatar
        }
      })
    })

    let imageId = ''
    const handleFileUploaded = (rawData: ResponseType<ImageProps>) => {
      if (rawData.data._id) {
        imageId = rawData.data._id
      }
    }

    const onFormSubmit = (validationResult: boolean) => {
      if (validationResult) {
        const newPayload = editMode.value === 'user' ? {
          nickName: nameVal.value,
          description: descriptionVal.value
        } : {
          title: nameVal.value,
          description: descriptionVal.value,
          avatar: column.value.avatar._id
        }

        if (imageId) {
          newPayload.avatar = imageId
        }

        let actionName
        let id
        if (editMode.value === 'user') {
          actionName = 'updateUser'
          id = user.value._id
        } else {
          actionName = 'updateColumn'
          id = columnId.value
        }

        store.dispatch(actionName, { id, payload: newPayload }).then(() => {
          createMessage(' 2 ', 'success', 2000)
          setTimeout(() => {
            router.push({ name: 'home' })
          }, 2000)
        })
      }
    }

    const titleRules: RulesProp = [
      { type: 'required', message: '' }
    ]

    const descriptionRules: RulesProp = [
      { type: 'required', message: '' }
    ]

    const beforeUpload = (file: File) => {
      const { passed, error } = beforeUploadCheck(file, { format: ['image/jpeg', 'image/png'], size: 1 })
      if (error === 'format') {
        createMessage(' JPG/PNG !', 'error')
      }
      if (error === 'size') {
        createMessage(' 1Mb', 'error')
      }
      return passed
    }

    return {
      onFormSubmit,
      titleRules,
      descriptionRules,
      beforeUpload,
      handleFileUploaded,
      uploadedData,
      editMode,
      nameVal,
      descriptionVal
    }
  }
})

</script>

