<template>
<teleport to="#modal">
  <div class="modal d-block"  tabindex="-1" v-if="isVisable">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">{{ title }}</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"
          @click="onClose" >
        </button>
      </div>
      <div class="modal-body">
        <slot></slot>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="onClose"></button>
        <button type="button" class="btn btn-primary" @click="onConfirm"></button>
      </div>
    </div>
  </div>
</div>
</teleport>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import useDOMCreate from '@/hook/useDOMCreate'

export default defineComponent({
  props: {
    isVisable: {
      type: Boolean,
      default: false
    },
    title: {
      type: String
    }
  },
  emits: ['modal-on-close', 'modal-on-confirm'],
  setup (props, context) {
    useDOMCreate('modal')
    const onClose = () => {
      context.emit('modal-on-close')
    }
    const onConfirm = () => {
      context.emit('modal-on-confirm')
    }
    return {
      onClose,
      onConfirm
    }
  }
})
</script>

<style scoped>

</style>
