
import { defineComponent } from 'vue'
import useDOMCreate from '@/hook/useDOMCreate'

export default defineComponent({
  props: {
    isVisable: {
      type: Boolean,
      default: false
    },
    title: {
      type: String
    }
  },
  emits: ['modal-on-close', 'modal-on-confirm'],
  setup (props, context) {
    useDOMCreate('modal')
    const onClose = () => {
      context.emit('modal-on-close')
    }
    const onConfirm = () => {
      context.emit('modal-on-confirm')
    }
    return {
      onClose,
      onConfirm
    }
  }
})
