<template>
  <div class="create-post-page">
    <h4>{{ isEditMode ? ' ' : '' }}</h4>
    <uploader
      :before-upload="beforeUpload"
      @file-uploaded="handleFileUploaded"
      :uploaded="uploadedData"
      action="/upload"
      class="d-flex align-items-center justify-content-center bg-light text-secondary w-100 my-4">
      <h2></h2>
      <template #loading>
        <div class="d-flex">
          <div class="spinner-border text-secondary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
          <h2></h2>
        </div>
      </template>
      <template #uploaded="dataProps">
        <img :src="dataProps.uploadedData.data.url"/>
      </template>
    </uploader>
    <validate-form @form-submit="onFormSubmit" class="mb-5">
      <div class="mb-3">
        <label class="form-label"></label>
        <validate-input
          :rules="titleRules"
          v-model="titleVal"
          placeholder=""
          type="text"
        />
      </div>
      <div class="mb-3">
        <label class="form-label"></label>
        <validate-input
          rows="10"
          type="text"
          tag="textarea"
          placeholder=""
          :rules="contentRules"
          v-model="contentVal"
        />
      </div>
      <template #submit>
        <button class="btn btn-primary btn-large">{{ isEditMode ? ' ' : '' }}</button>
      </template>
    </validate-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter, useRoute } from 'vue-router'
import { GlobalDataProps, PostProps, ResponseType, ImageProps } from '@/store'
import ValidateInput, { RulesProp } from '@/components/ValidateInput.vue'
import ValidateForm from '@/components/ValidateForm.vue'
import Uploader from '@/components/Uploader.vue'
import { beforeUploadCheck } from '@/utils/helper'
import createMessage from '@/components/createMessage'

export default defineComponent({
  name: 'Login',
  components: {
    ValidateInput,
    ValidateForm,
    Uploader
  },
  setup () {
    const titleVal = ref('')
    const router = useRouter()
    const route = useRoute()
    const store = useStore<GlobalDataProps>()
    const isEditMode = !!route.query.id
    const uploadedData = ref({ data: {} })

    onMounted(() => {
      if (isEditMode) {
        store.dispatch('fetchPost', route.query.id).then((rawData: ResponseType<PostProps>) => {
          const currentPost = rawData.data
          if (currentPost.image) {
            uploadedData.value = {
              data: currentPost.image
            }
          }
          titleVal.value = currentPost.title || ''
          contentVal.value = currentPost.content || ''
        })
      }
    })

    const titleRules: RulesProp = [
      { type: 'required', message: '' }
    ]

    const contentVal = ref('')
    const contentRules: RulesProp = [
      { type: 'required', message: '' }
    ]

    let imageId = ''
    const handleFileUploaded = (rawData: ResponseType<ImageProps>) => {
      if (rawData.data._id) {
        imageId = rawData.data._id
      }
    }

    const onFormSubmit = (result: boolean) => {
      if (result) {
        const { column, _id } = store.state.user
        if (column) {
          const newPost: PostProps = {
            title: titleVal.value,
            content: contentVal.value,
            column,
            author: _id
          }
          if (imageId) {
            newPost.image = imageId
          }

          const actionName = isEditMode ? 'updatePost' : 'createPost'
          const sendData = isEditMode ? {
            id: route.query.id,
            payload: newPost
          } : newPost

          store.dispatch(actionName, sendData).then(() => {
            createMessage(' 2 ', 'success', 2000)
            setTimeout(() => {
              router.push({ name: 'column', params: { id: column } })
            }, 2000)
          })
        }
      }
    }

    const beforeUpload = (file: File) => {
      const { passed, error } = beforeUploadCheck(file, { format: ['image/jpeg', 'image/png'], size: 1 })
      if (error === 'format') {
        createMessage(' JPG/PNG !', 'error')
      }
      if (error === 'size') {
        createMessage(' 1Mb', 'error')
      }
      return passed
    }

    return {
      titleRules,
      titleVal,
      contentVal,
      contentRules,
      uploadedData,
      onFormSubmit,
      beforeUpload,
      handleFileUploaded,
      isEditMode
    }
  }
})
</script>
<style scoped>
.create-post-page :deep(.file-upload-container) {
  height: 200px;
  cursor: pointer;
}

.create-post-page :deep(.file-upload-container img) {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>
