<template>
  <div class="user-profile d-flex">
    <img
      class="user-profile_avatar rounded-circle me-2 img-thumbnail"
      :src='fitUrl'/>
    <div class="text-start py-1">
      <h6 class="mb-1">{{ user.nickName }}</h6>
      <span class="text-muted align-baseline">{{ user.description }}</span>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, computed } from 'vue'
import { UserProps } from '@/store'
import { addColumnAvatar } from '@/utils/helper'

export default defineComponent({
  props: {
    user: {
      type: Object as PropType<UserProps>,
      required: true
    }
  },
  setup (props) {
    const fitUrl = computed(() => {
      addColumnAvatar(props.user, 50, 50)
      const { avatar } = props.user
      return avatar && avatar.fitUrl
    })
    return {
      fitUrl
    }
  }
})
</script>

<style scoped>

.user-profile_avatar {
  width: 50px;
  height: 50px;
}
</style>
